<template>
  <div class="categoryList">
    <h1>分类列表</h1>
    <el-table :data="items">
      <el-table-column sortable prop="_id" label="ID" width="300"></el-table-column>
      <el-table-column sortable prop="parent.name" label="上级分类"></el-table-column>
      <el-table-column sortable prop="name" label="分类名称"></el-table-column>
      <el-table-column sortable fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <el-button type="primary" size="small" @click="$router.push(`/categories/edit/${scope.row._id}`)">编辑</el-button>
          <el-button type="danger" size="small" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return{
      items:[],
    }
  },
  methods:{
    // 获取分类
    async fetch(){
      const res = await this.$http.get('rest/categories')
      this.items = res.data
    },
    // 删除分类
    async remove(row){
      this.$confirm(`确定要删除分类 "${row.name}" 吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/categories/${row._id}`)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  },
  created(){
    this.fetch()
  }
}
</script>
