<template>
  <div class="categoryList">
    <h1>举报列表</h1>
    <el-table :data="items">
      <el-table-column sortable prop="_id" label="举报ID" width="280"></el-table-column>
      <el-table-column sortable prop="reportID._id" label="被举报用户ID"></el-table-column>
      <el-table-column sortable prop="reportID.username" label="被举报用户"></el-table-column>
      <el-table-column sortable prop="reportContent" label="举报内容"></el-table-column>
      <el-table-column sortable fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <!-- <el-button type="primary" size="small" @click="$router.push(`/users/edit/${scope.row._id}`)">编辑</el-button> -->
          <el-button type="danger" size="small" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return{
      items:[],
    }
  },
  methods:{
    // 获取举报信息
    async fetch(){
      const res = await this.$http.get('rest/reports')
      this.items = res.data
    },
    // 删除举报信息
    async remove(row){
      this.$confirm(`确定要删除举报信息 "${row.username}" 吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/reports/${row._id}`)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  },
  created(){
    this.fetch()
  }
}
</script>
