<template>
  <div class="categoryList">
    <h1>用户列表</h1>
    <el-table :data="items">
      <el-table-column sortable prop="_id" label="用户ID" width="280"></el-table-column>
      <el-table-column sortable prop="username" label="用户名称"></el-table-column>
      <el-table-column sortable prop="avatar" label="用户头像">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" style="height:3rem;">
        </template>
      </el-table-column>
      <el-table-column sortable prop="username" label="用户名称"></el-table-column>
      <el-table-column sortable prop="createdAt" label="注册时间">
        <template slot-scope="scope">
          <span>{{scope.row.createdAt | date}}</span>
        </template>
      </el-table-column>
      <el-table-column sortable fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <!-- <el-button type="primary" size="small" @click="$router.push(`/users/edit/${scope.row._id}`)">编辑</el-button> -->
          <el-button type="danger" size="small" @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return{
      items:[],
    }
  },
  methods:{
    // 获取用户
    async fetch(){
      const res = await this.$http.get('rest/users')
      this.items = res.data
    },
    // 删除用户
    async remove(row){
      this.$confirm(`确定要删除用户 "${row.username}" 吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/users/${row._id}`)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  },
  created(){
    this.fetch()
  }
}
</script>
