<template>
  <div class="categoryList">
    <h1>物品列表</h1>
    <el-table :data="items">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="物品 ID">
              <span>{{ props.row._id }}</span>
            </el-form-item>
            <el-form-item label="物品名称">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label="物品类型">
              <span>{{ props.row.type[0].name }}</span>
            </el-form-item>
            <el-form-item label="物品详情">
              <span>{{ props.row.description }}</span>
            </el-form-item>
            <el-form-item label="物品图片">
               <span v-for="pic in props.row.photo" :key="pic">
                  <el-image
                    class="mr-3"
                    style="width: 100px; height: 100px"
                    :src="pic"
                    fits="fit"></el-image>
                </span>
            </el-form-item>
            <el-form-item label="物品原价">
              <span>{{ props.row.originalPrice }} 元</span>
            </el-form-item>
            <el-form-item label="物品售价">
              <span>{{ props.row.price }} 元</span>
            </el-form-item>
            <el-form-item label="发布人 ID">
              <span>{{ props.row.userId._id }}</span>
            </el-form-item>
            <el-form-item label="发布人">
              <span>{{ props.row.userId.username }}</span>
            </el-form-item>
            <el-form-item label="联系方式">
              <span>{{ props.row.userId.tel }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column sortable prop="_id" label="物品ID" width="210"></el-table-column>
      <el-table-column sortable prop="name" label="物品名称" width="300"></el-table-column>
      <!-- <el-table-column sortable prop="type[0].name" label="物品类型"></el-table-column> -->
      <el-table-column sortable prop="userId._id" label="发布人ID" width="210"></el-table-column>
      <el-table-column sortable prop="userId.username" label="发布人"></el-table-column>
      <el-table-column sortable prop="userId.tel" label="联系方式"></el-table-column>
      <!-- <el-table-column sortable prop="originalPrice" label="原价（元）"></el-table-column> -->
      <!-- <el-table-column sortable prop="price" label="价格（元）" width="150"></el-table-column> -->
      <el-table-column sortable fixed="right" label="操作" width="180">
        <template slot-scope="scope">
          <!-- <el-button type="primary" size="small"
            @click="$router.push(`/goods/edit/${scope.row._id}`)">编辑</el-button> -->
          <el-button type="danger" size="small"
          @click="remove(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data(){
    return{
      items:[],
      fits: ['fill', 'contain', 'cover', 'none', 'scale-down'],
    }
  },
  methods:{
    // 获取物品
    async fetch(){
      const res = await this.$http.get('rest/goods')
      this.items = res.data
    },
    // 删除物品
    async remove(row){
      this.$confirm(`确定要删除物品 "${row.name}" 吗?`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async() => {
          await this.$http.delete(`rest/goods/${row._id}`)
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          this.fetch()
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }
  },
  created(){
    this.fetch()
  }
}
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>

